import React, { Component } from 'react';


const TableHeader = ()=> {
	return (
		<thead>
				<tr>
					<th>Girl Name</th>
					<th>How Hot</th>
				</tr>
			</thead>
	);
}

const TableBody = props => {
	//console.log(props);
	//typeof(props.hotnessData);
	//console.log(typeof(props.hotnessData));
	//console.log(result);
	//let data = Array.from(props.hotnessData);
	//console.log(data);
	const rows = props.hotnessData.map((row,index) => {
		return (
			<tr key = {index}>
				<td>{row.name}</td>
				<td>{row.hotness}</td>
				 <td>
					<button onClick={() => props.removeG(index)}>Delete</button>
				</td>
			</tr>
		)
	});
	//console.log(rows);
	return (
		
		<tbody> {rows} </tbody>
		
	);
}

class Table extends Component{
	render(){
		console.log(this.props)
		//console.log(hotnessData)
		const {hotnessData, removeG} = this.props;
		//console.log({t})
		
		return (
			<table>
				<TableHeader/>
				<TableBody hotnessData = {hotnessData} removeG = {removeG}/>
			</table>
		)
	}
}

export default Table;

