import React, {Component} from 'react';
import Table from './Table.js';
import Form from './Form.js';

class App extends Component {
	state = {
		girlsHotness: [],
	}
	
	removeGirl= index => {
		const { girlsHotness } = this.state; 
		//console.log(girlsHotness);
		this.setState({
			girlsHotness: girlsHotness.filter((girl, i ) => {
				return i !== index;
			})
		})
	}

	handleSubmit = girl => {
		console.log(girl);
		this.setState({ girlsHotness: [...this.state.girlsHotness, girl] });
		
	}
	
	render(){
		const { girlsHotness } = this.state; 
		console.log(this.state.girlsHotness); // This is showing Array(4) in console
		console.log( girlsHotness ); // But why this is 'Undefined'?
		
		return (
			<div className="container">
				<h1>Hi guys, this the is the hotness List of the girls</h1>
				<Table hotnessData = {girlsHotness} removeG = {this.removeGirl}/>
				<Form handleSubmit={this.handleSubmit}/>
			</div>
		);
	}
}

export default App;